.container {
	margin: 0 auto;
	width: 100%;
}

.container img {
	width: 150px;
	margin-top: 45px;
	background-color: #002828;
	padding: 35px;
	box-shadow: 5px 5px 12px;
	animation: moveIn ease 0.8s;
	animation-iteration-count: 1;
	animation-fill-mode: forwards;
}

.container h1 {
	color: #fcfcf0;
	font-size: 3em;
}
.info p {
	font-size: 25px;
	white-space: pre-wrap;
	color: #fcfcf0;
}

.info p a{
	text-decoration: none;
	color: #fcfcf1;
	transition: all 0.5s ease-in-out;
}

.info p a:hover{
	font-size: 28px;
	text-decoration: underline;
}
@keyframes moveIn {
	0% {
		marign-right:100%;
		width:0px;
	}
	100% {
		margin-right:0%;
		width: 150px;
	}
}

